<template>
  <el-dialog
    class="popup-use-imprest-detail"
    title="未发行额度处理申请详情"
    width="50%"
    :visible.sync="show"
    destroy-on-close
    @closed="onClose"
  >
    <div v-loading="isLoading">
      <info-block>
        <info-item label="活动名称：">活动1</info-item>
        <info-item label="类型：">活动返还</info-item>
        <info-item label="预算额度：">{{ 100000 | fmtMoney }}</info-item>
        <info-item label="备用额度：">{{ 100000 | fmtMoney }}</info-item>
      </info-block>
      <info-block>
        <info-item label="申请人：">担当1</info-item>
        <info-item label="申请时间：">2020-5-25 12:00</info-item>
        <info-item label="编辑人：">担当2</info-item>
        <info-item label="编辑时间：">2020-5-25 12:00</info-item>
        <info-item label="申请状态："><appl-sts-icon :status="0" /></info-item>
        <info-item label="审批时间：">-</info-item>
        <info-item label="备注："></info-item>
      </info-block>
      <info-block>
        <info-item label="附件：" block>
          <ul class="attachment-list" v-if="info.attachments">
            <li v-for="(att, index) in info.attachments" :key="index">
              <span>{{ index + 1 }}. {{ att.name }}</span>
              <el-link icon="el-icon-download" @click="onDownLoad(att.url)">下载</el-link>
            </li>
          </ul>
        </info-item>
      </info-block>

      <el-divider></el-divider>

      <option-btns
        :status="isAppling"
        :auth="optionsAuth"
        :aid="info.id"
        type="popup"
        verify="serviceVerifyAppl"
        @onEdit="toEditAppl"
        @onCancel="changeCancel"
        @onReject="changeNoPass"
        @onPass="changePass"
        @onClose="show = false"
      ></option-btns>
    </div>
  </el-dialog>
</template>

<script>
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'
import OptionBtns from '@/components/OptionBtns'
import ApplStsIcon from '@/components/status/ApplStsIcon'
import { mapGetters } from 'vuex'

export default {
  name: 'popup-use-imprest-detail',
  components: {
    InfoBlock,
    InfoItem,
    OptionBtns,
    ApplStsIcon
  },
  model: {
    prop: 'visible',
    event: 'change'
  },
  props: {
    visible: Boolean,
    sid: [Number, String]
  },
  data () {
    return {
      show: false,
      isLoading: false,
      info: {}
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    isAppling () {
      if (this.info.status === 1 || this.info.status === 4) return 'isAppling'
      return ''
    },

    optionsAuth () {
      // 判断是否可编辑或者管理员
      if (!this.accountInfo.isAdmin) return 'isAdmin'
      if (this.info.deptId === this.accountInfo.orgId) return 'isEditor'
      return ''
    }
  },
  watch: {
    visible (val) {
      this.show = val

      val && this.getData(this.sid)
    }
  },
  methods: {
    getData (id) {
      this.isLoading = true
      this.$http.serviceGetApplDetail({ id })
        .then(res => {
          this.info = { ...res }
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    toEditAppl () {
      this.$emit('popupEdit', this.sid)
    },

    changePass () {
      this.changeApplicationStatus(0)
    },

    changeNoPass () {
      this.changeApplicationStatus(2)
    },

    changeCancel () {
      this.changeApplicationStatus(3)
    },

    // 更新申请状态
    changeApplicationStatus (status) {
      this.isLoading = true
      this.$http.serviceUpdateAcctApplSts({ status })
        .then(res => {
          this.info.status = status
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    onClose () {
      this.$emit('update:visible', this.show)
    }
  }
}
</script>

<style lang="scss" scoped>
.attachment-list {
  margin: 0;
  padding: 0;

  li {
    display: flex;
    justify-content: space-between;
    padding-right: 100px;

    .el-link {
      font-size: 13px;
    }
  }
}
</style>
