<template>
  <div class="option-btns">

    <template v-if="isEditor">
      <el-button size="small" type="warning" plain icon="el-icon-edit" @click="onEdit">编辑</el-button>
      <el-button size="small" type="warning" plain icon="el-icon-circle-close" @click="onCancel">取消申请</el-button>
    </template>

    <template v-if="isAdmin && isAppling">
      <el-button size="small" type="danger" plain icon="el-icon-remove-outline" :loading="onRejectLoading" @click="onVerify('onReject')">拒绝申请</el-button>
      <el-button size="small" type="success" plain icon="el-icon-circle-check" :loading="onPassLoading" @click="onVerify('onPass')">审批通过</el-button>
    </template>

    <el-button v-if="type === 'page'" size="small" plain icon="el-icon-back" @click="onClose">返回</el-button>
    <el-button v-if="type === 'popup'" size="small" plain icon="el-icon-close" @click="onClose">关闭</el-button>
  </div>
</template>

<script>
export default {
  name: 'option-btns',
  props: {
    status: String,
    auth: String,
    verify: String,
    aid: [Number, String],
    type: {
      type: String,
      default: 'page'
    }
  },
  data () {
    return {
      cancleLoading: false,
      onRejectLoading: false,
      onPassLoading: false
    }
  },
  computed: {
    isAppling () {
      return this.status === 'isAppling'
    },

    isAdmin () {
      return this.auth === 'isAdmin'
    },

    isEditor () {
      return this.auth === 'isEditor'
    }
  },
  methods: {
    onEdit () {
      this.$emit('onEdit')
    },

    onCancel () {
      this.$confirm('是否确认取消该申请？', { type: 'warning' })
        .then(() => {
          this.$emit('onCancel')
        })
    },

    onReject () {
      this.$confirm('是否确认拒绝该申请？', { type: 'error' })
        .then(() => {
          this.$emit('onReject')
        })
    },

    onPass () {
      this.$confirm('是否确认通过该申请？', { type: 'success' })
        .then(() => {
          this.$emit('onPass')
        })
    },

    // 审批前验证版本变更
    onVerify (callback) {
      this[callback + 'Loading'] = true
      this.$http[this.verify]({ id: this.aid })
        .then(res => {
          if (res.isChanged) {
            this.$alert('该申请已变更，请刷新本页查看最新内容', { type: 'warning' })
          } else {
            this[callback]()
          }
        })
        .finally(() => {
          this[callback + 'Loading'] = false
        })
    },

    onClose () {
      this.$emit('onClose')
    }
  }
}
</script>

<style lang="scss" scoped>
.option-btns {
  text-align: right;

  .el-button {
    margin-left: 10px;
    width: 100px;
  }
}
</style>
